@import '../../responsiveScss/_media';

.about-container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include md {
        display: block;
    }

    p {
        line-height: 35px;
        font-size: 19px;
        font-weight: 500;
        color: #817474;
        text-align: justify;

        @include md {
            font-size: 14px;
            line-height: 25px;
        }
    }

    p, img {
        flex: 0 1 45%;
        width: 45%;

        @include md {
            width: 100%;
        }
    }
}