@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#Navbar{position:fixed;z-index:10;top:0;left:0;width:100%;padding:15px;display:flex;justify-content:space-between;align-items:center;background-color:#202020;transition:0.6s}@media (max-width: 992px){#Navbar{padding:5px 25px}}#Navbar .Logo{width:50px;height:50px;outline:none;transition:0.6s}@media (max-width: 992px){#Navbar .Logo{width:35px;height:35px}}#Navbar .Logo.hover,#Navbar .Logo:focus{-webkit-filter:invert(40%);filter:invert(40%)}#Navbar .Logo img{width:100%;height:100%;transition:0.6s}#Navbar ul{position:relative;display:flex;justify-content:center;align-items:center}@media (max-width: 992px){#Navbar ul{position:absolute;top:40px;left:0;width:100%;height:100vh;text-align:center;overflow:auto;background:#fff;visibility:hidden}}#Navbar ul li{position:relative;list-style:none}#Navbar ul li a{position:relative;margin:0 15px;text-decoration:none;color:#fff;letter-spacing:1px;font-size:18px;font-weight:500;font-family:Poppins-Regular;outline:none;transition:0.6s}@media (max-width: 992px){#Navbar ul li a{font-size:1em;transition:0s}}#Navbar ul li a:hover{-webkit-filter:invert(40%);filter:invert(40%)}#Navbar ul li a:focus{text-decoration:underline}#Navbar ul li a.Phone{padding:8px;border:solid;border-radius:25px}#Navbar ul .separator{background-color:#fff;height:25px;width:6px;border-radius:25px}#Navbar .toggle{display:none}@media (max-width: 992px){#Navbar .toggle{position:relative;display:block;width:30px;height:30px;cursor:pointer}#Navbar .toggle:after,#Navbar .toggle:before{content:'';position:absolute;width:100%;height:2px;background:#fff;z-index:1;bottom:4px}#Navbar .toggle:before{box-shadow:0 10px 0 #fff;bottom:0px;top:4px}}#Navbar.develop ul{border-top:1px solid black;visibility:visible;opacity:1;display:block;margin-top:5px}#Navbar.develop ul a{display:inline-block;margin:10px 0;font-size:1em;color:#000}

#Home{position:relative;width:100%;height:100vh;box-sizing:border-box}#Home .Parallax-wrapper{height:100vh;z-index:-1;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover;background-image:url(/static/media/BackgroundHome.a1c3bdad.jpg)}#Home .Parallax-wrapper .Slogan-Container{margin-top:75px;width:100%;height:15%;background-color:#fff;display:flex;flex-direction:column;justify-content:center;align-items:center}#Home .Parallax-wrapper .Slogan-Container h1{color:#252525;font-weight:600;font-size:40px;letter-spacing:1px;text-shadow:0px 4px 4px rgba(99,97,97,0.1);font-family:'Permanent Marker', cursive}#Home .Parallax-wrapper .Slogan-Container h3{color:#444343;font-weight:600;letter-spacing:1px;text-shadow:0px 4px 4px rgba(0,0,0,0.1)}#Home .Parallax-wrapper img{width:100%;height:85%}@media (max-width: 992px){#Home .Parallax-wrapper{background:url(/static/media/Responsive.5cf36f73.jpg) no-repeat center center;background-size:cover}}

.contact-container{height:500px;display:flex;justify-content:space-between}@media (max-width: 992px){.contact-container{display:block;height:auto}}.contact-container .map{flex:0 1 45%;border:0px solid #b8b5b5;border-radius:25px}@media (max-width: 992px){.contact-container .map{width:100%;height:300px}}.contact-container .contact-info-container{flex:0 1 45%;height:100%;max-height:100%}.contact-container .contact-info-container .contact-info{display:inline-flex;width:100%;align-items:center;justify-content:center;height:166px}.contact-container .contact-info-container .contact-info img{width:64px;height:64px}.contact-container .contact-info-container .contact-info a{text-decoration:none}.contact-container .contact-info-container .contact-info a h3{margin-left:15px;color:#000}.contact-container .contact-info-container .contact-info a:hover,.contact-container .contact-info-container .contact-info a:focus{-webkit-filter:invert(40%);filter:invert(40%);outline:none}@media (max-width: 768px){.contact-container .contact-info-container .contact-info a h3{font-size:16px}}.contact-container .contact-info-container .divider{background-color:lightgray;height:2px;width:100%}form{margin-top:75px;position:relative;display:flex;flex-wrap:wrap;justify-content:space-between}@media (max-width: 992px){form{width:100%;margin-top:20px}}form input{-webkit-appearance:none;flex:0 1 45%;margin-bottom:20px}@media (max-width: 768px){form input{flex:0 1 100%}}form textarea{-webkit-appearance:none;flex:0 1 100%;margin-bottom:20px}form a{-webkit-appearance:none;display:block;width:100%;height:45px;background:#51ACFF;color:white;font-size:18px;font-family:Poppins-Regular;border-radius:35px;box-shadow:0px 0px 4px rgba(0,0,0,0.25);border:none;transition:0.6s;outline:none;color:black;background:white;border:solid 1px gray;text-align:center;padding-top:10px;text-decoration:none}form a:hover,form a:focus{transition:0.6s;background:black;color:white}

.section{margin:50px 10% 0}.section .title{display:flex;color:#313030;letter-spacing:1px;font-size:30px;text-transform:uppercase;margin-bottom:20px}@media (max-width: 768px){.section .title{font-size:25px}}.section .title::before{content:"";display:block;width:8px;height:35px;background-color:lightgray;margin:0px 10px 10px 0}@media (max-width: 768px){.section{padding:0 5%}}

.classicInput{width:100%;height:60px;border-radius:150px;border:none;outline:none;box-shadow:0px 0px 4px rgba(0,0,0,0.25);font-family:Poppins-Regular;font-size:18px;padding-left:20px;transition:0.6s;border:solid 1px lightgray}.classicInput::-webkit-input-placeholder{color:rgba(79,72,72,0.7)}.classicInput:-ms-input-placeholder{color:rgba(79,72,72,0.7)}.classicInput::-ms-input-placeholder{color:rgba(79,72,72,0.7)}.classicInput::placeholder{color:rgba(79,72,72,0.7)}.classicInput:hover,.classicInput:focus{background-color:rgba(190,190,190,0.15);transition:0.6s}

.classicTextarea{display:block;padding-left:20px;padding-top:10px;resize:vertical;min-height:100px;height:100px;border-radius:35px;border:none;outline:none;box-shadow:0px 0px 4px rgba(0,0,0,0.25);font-family:Poppins-Regular;font-size:18px;transition:0.6s;border:solid 1px lightgray}.classicTextarea::-webkit-input-placeholder{color:rgba(79,72,72,0.7)}.classicTextarea:-ms-input-placeholder{color:rgba(79,72,72,0.7)}.classicTextarea::-ms-input-placeholder{color:rgba(79,72,72,0.7)}.classicTextarea::placeholder{color:rgba(79,72,72,0.7)}.classicTextarea:hover,.classicTextarea:focus{background-color:rgba(190,190,190,0.15);transition:0.6s}

.about-container{display:flex;justify-content:space-between;align-items:center}@media (max-width: 768px){.about-container{display:block}}.about-container p{line-height:35px;font-size:19px;font-weight:500;color:#817474;text-align:justify}@media (max-width: 768px){.about-container p{font-size:14px;line-height:25px}}.about-container p,.about-container img{flex:0 1 45%;width:45%}@media (max-width: 768px){.about-container p,.about-container img{width:100%}}

#Footer{height:50px;width:100%;display:flex;align-items:center;justify-content:space-evenly;background-color:#202020;color:white;margin-top:30px;padding:10px 0 10px 0}#Footer .Mentions{z-index:1}#Footer a{text-decoration:none;color:#fff}#Footer a:hover{-webkit-filter:invert(40%);filter:invert(40%)}#Footer .MadeBy{margin-left:10px;z-index:2}

#Services .container{display:flex;justify-content:space-evenly;align-items:baseline;flex-wrap:wrap}@media (max-width: 749px){#Services .container{justify-content:center}}#Services .container .card{text-align:center;flex:0 1 250px;padding:20px 35px}#Services .container .card img{max-height:70px}#Services .container .card p{text-align:center}@media (max-width: 768px){#Services .container{font-size:14px;line-height:25px}#Services .container p{text-align:justify}}

*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

