@import '../../responsiveScss/_media';

@mixin transition {
    transition: 0.6s;
}

#Navbar {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(32, 32, 32);
    @include transition();

    @include lg {
        padding: 5px 25px;
    }

    .Logo {
        width: 50px;
        height: 50px;
        outline: none;
        @include transition();

        @include lg {
            width: 35px;
            height: 35px;
        }

        &.hover, &:focus {
            filter: invert(40%);
        }

        img {
            width: 100%;
            height: 100%;
            @include transition();
        }
    }

    ul {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

        @include lg {
            position: absolute;
            top: 40px;
            left: 0;
            width: 100%;
            height: 100vh;
            text-align: center;
            overflow: auto;
            background: #fff;
            visibility: hidden;
        }
        
        li {
            position: relative;
            list-style: none;
        
            a {
                position: relative;
                margin: 0 15px;
                text-decoration: none;
                color: rgb(255, 255, 255);
                letter-spacing: 1px;
                font-size: 18px;
                font-weight: 500;
                font-family: Poppins-Regular;
                outline: none;
                @include transition();

                @include lg {
                    font-size: 1em;
                    transition: 0s;
                }


                &:hover {
                    filter: invert(40%);
                }
    
                &:focus {
                    text-decoration: underline;
                }
                    
                &.Phone {
                    padding: 8px;
                    border: solid;
                    border-radius: 25px;
                }
            }

        }
        .separator {
            background-color: #fff;
            height: 25px;
            width: 6px;
            border-radius: 25px;
        }
    }
    .toggle {
        display: none;
        
        @include lg {
            position: relative;
            display: block;
            width: 30px;
            height: 30px;
            cursor: pointer;

            &:after  {
                content: '';
                position: absolute;
                width: 100%;
                height: 2px;
                background: rgb(255, 255, 255);
                z-index: 1;
                bottom: 4px;
            }

            &:before {
                @extend .toggle:after;
                box-shadow: 0 10px 0 rgb(255, 255, 255);
                bottom: 0px;
                top: 4px;
            }
        }
    }

    &.develop ul {
        border-top: 1px solid black;
        visibility: visible;
        opacity: 1;
        display: block;
        margin-top: 5px;

        a {
            display: inline-block;
            margin: 10px 0;
            font-size: 1em;
            color: #000;
        }
    }
}