@import '../../responsiveScss/_media';

#Services {
    .container {
        display: flex;
        justify-content: space-evenly;
        align-items: baseline;
        flex-wrap: wrap; 
        
        @include rwd(749) {
            justify-content: center;
        }

        .card {
            text-align: center;
            flex: 0 1 250px;
            padding: 20px 35px;

            img {
                max-height: 70px;
            }

            p {
                text-align: center;
            }
        }

        @include md {
            font-size: 14px;
            line-height: 25px;

            p {
                text-align: justify;
            }
        }
    }
}