.classicInput {
    width: 100%;
    height: 60px;
    border-radius: 150px;
    border: none;
    outline: none;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    font-family: Poppins-Regular;
    font-size: 18px;
    padding-left: 20px;
    transition: 0.6s;
    border: solid 1px lightgray;
    
    &::placeholder {
        color: rgba(79, 72, 72, 0.7);
    }

    &:hover, &:focus {
        background-color: rgba(190, 190, 190, 0.150);
        transition: 0.6s;
    }
}