@import '../../responsiveScss/_media';

.contact-container {
    height: 500px;
    display: flex;
    justify-content: space-between;

    @include lg {
        display: block;
        height: auto;
    }
    
    .map {
        flex: 0 1 45%;
        border: 0px solid rgb(184, 181, 181);
        border-radius: 25px;
        @include lg {
            width: 100%;
            height: 300px;
        }
    }

    .contact-info-container {
        flex: 0 1 45%;
        height: 100%;
        max-height: 100%;        

        .contact-info {
            display: inline-flex;
            width: 100%;
            align-items: center;
            justify-content: center;
            height: 166px;
            
            img {
                width: 64px;
                height: 64px;
            }

            a {
                text-decoration: none;

                h3 {
                    margin-left: 15px;
                    color: #000;
                }

                &:hover, &:focus {
                    filter:invert(40%);
                    outline: none;
                }
            }

            @include md {
                a {
                    h3 {
                        font-size: 16px;
                    }
                }
            }
        }
        .divider {
            background-color: lightgray;
            height: 2px;
            width: 100%;
        }
    }
}

form {
    margin-top: 75px;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include lg {
        width: 100%;
        margin-top: 20px;
    }

    input {
        -webkit-appearance: none;
        flex: 0 1 45%;
        margin-bottom: 20px;

        @include md {
            flex: 0 1 100%;
        }
    }

    textarea {
        -webkit-appearance: none;
        flex: 0 1 100%;
        margin-bottom: 20px;
    }

    a {
        -webkit-appearance: none;
        display: block;
        width: 100%;
        height: 45px;
        background: #51ACFF;
        color: white;
        font-size: 18px;
        font-family: Poppins-Regular;
        border-radius: 35px;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border: none;
        transition: 0.6s;
        outline: none;
        color: black;
        background:  white;
        border: solid 1px gray;
        text-align: center;
        padding-top: 10px;
        text-decoration: none;
    
        &:hover, &:focus {
            transition: 0.6s;
            background: black;
            color: white;
        }
    }
}