#Footer {
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgb(32, 32, 32);
    color: white;
    margin-top: 30px;
    padding: 10px 0 10px 0;

    .Mentions {
        z-index: 1;
    }

    a {
        text-decoration: none;
        color: #fff;

        &:hover {
            filter: invert(40%);
        }
    }

    .MadeBy {
        margin-left: 10px;
        z-index: 2;
    }


}