@import '../../responsiveScss/_media';

.section {
    margin: 50px 10% 0;

    .title {
        display: flex;
        color: rgb(49, 48, 48);
        letter-spacing: 1px;
        font-size: 30px;
        text-transform: uppercase;
        margin-bottom: 20px;

        @include md {
            font-size: 25px;
        }

        &::before {
            content: "";
            display: block;
            width: 8px;
            height: 35px;
            background-color: lightgray;
            margin: 0px 10px 10px 0;
        }
    }

    @include md {
        padding: 0 5%;
    }
}