@import '../../responsiveScss/_media';
@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');

#Home {
    position: relative;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;

    .Parallax-wrapper {
        height: 100vh;
        z-index: -1;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url('BackgroundHome.jpg');

        .Slogan-Container {
            margin-top: 75px;
            width: 100%;
            height: 15%;
            background-color: #fff;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            h1 {
                color: rgb(37, 37, 37);
                font-weight: 600;
                font-size: 40px;
                letter-spacing: 1px;
                text-shadow: 0px 4px 4px rgba(99, 97, 97, 0.1);
                font-family: 'Permanent Marker', cursive;
            }
            h3 {
                color:rgb(68, 67, 67);
                font-weight: 600;
                letter-spacing: 1px;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
            }
        }  

        img {
            width: 100%;
            height: 85%;
        }

        @include lg {
            background: url('./Responsive.jpg') no-repeat center center;
            background-size: cover;
        }
    }
}