.classicTextarea {
    display: block;
    padding-left: 20px;
    padding-top: 10px;
    resize : vertical;
    min-height: 100px;
    height: 100px;
    border-radius: 35px;
    border: none;
    outline: none;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    font-family: Poppins-Regular;
    font-size: 18px;
    transition: 0.6s;
    border: solid 1px lightgray;

    &::placeholder {
        color: rgba(79, 72, 72, 0.7);
    }

    &:hover, &:focus {
        background-color: rgba(190, 190, 190, 0.150);
        transition: 0.6s;
    }
}